import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'

import MediaHomeView from '../views/media/HomeView.vue'
import MediaAboutView from '../views/media/AboutView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
  {
    path: '/',
    name: 'MediaHomeView',
    component: MediaHomeView
  },
  {
    path: '/I3p/1cX9/:id',
    name: 'MediaAboutView',
    component: MediaAboutView
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
