                  </div>
                  <div class="mx-2">
                    <p class="text-xs p-0 m-0"><i class="el-icon-arrow-right"></i></p>
                  </div>
                  <div>
                    <p class="poppins-light cursor-pointer hover:text-blue-500 text-sm p-0 m-0" @click="getViews('/')">{{ name }}</p>
                  </div>
                </div>
                
                <!-- <el-breadcrumb separator="/">
                  <el-breadcrumb-item class="poppins-thin" :to="{ path: '/' }">{{ name }}</el-breadcrumb-item>
                  <el-breadcrumb-item>...</el-breadcrumb-item>
