<script>
// import HelloWorld from '@/components/HelloWorld.vue'
import menu from '@/assets/json/ingredient.json'
export default {
  name: 'MediaAboutView',
  // components: { HelloWorld }
  data() {
    return {
      id: this.$route.params.id,
      menu: menu.materi,
      name: null,
      subname: null,
      materi: {}
    }
  },
  created() {
    this.take_name()
  },
  methods: {
    getViews: function(value){
			if (this.$route.path !== value) {
				this.$router.push(value);
			}
		},
    take_name: function() {
      for (let i = 0; i < this.menu.length; i++) {
        const element = this.menu[i];
        if (element.id == this.id) {
          this.name = element.title
          this.materi = element.lesson[0];
          this.subname = element.lesson[0].title;
        }
      }
    },
    take_source: function(value) {
      this.materi = value;
      this.subname = value.title;
    }
  },
}
</script>
